import { Component, OnInit } from '@angular/core';
import {MyserviceService} from '../../myservice.service';

@Component({
  selector: 'app-thirdpage',
  templateUrl: './thirdpage.component.html',
  styleUrls: ['./thirdpage.component.css']
})
export class ThirdpageComponent implements OnInit {
  numbers = [];
  greynumbers =[];
  constructor(private newservice:MyserviceService) { 
  	for(var i=0;i<15;i++){
      this.numbers[i] = `/images/${this.newservice.id}${i}.jpg`;
      this.greynumbers[i] = `/images/grey/${this.newservice.id}${i}.jpg`;
  	}
  }

  ngOnInit() {
  }

}



