import { Injectable } from '@angular/core';
import {Http,Headers,URLSearchParams} from '@angular/http';
import 'rxjs/add/operator/map';
import {Router} from "@angular/router";

@Injectable()
export class MyserviceService {

  constructor(private http: Http,private router: Router) {}

   obj= [];
   id = "";
   message = "";
  fetchdata(dat) {
    this.id = "";
    this.message = "Please wait for a moment images are being loaded";
    let data = new URLSearchParams();
      data.append('search', dat);
      this.http.post('/search',data).map((response) => response.text())
      .subscribe(value => {
         if(value === "success"){
           this.id = dat;
           this.message = "done";
         }
         else console.log("Error");
         })
       }

  getdata() {
      this.http.get('/searcheddata').map((response) => response.json())
      .subscribe(data => {
    this.obj = [];
    data.map(element => {
      this.obj.push(element.searchparam); 
        });
       })
     }

senddata(dat) {
    this.id = dat;
    this.router.navigate(['/thirdpage']);
}

}



